.section-4 {
    height: 100%;
    width: 100%;
    position: relative;

    
    &__slide{
        height: 100%;
        object-fit: cover;
        width: 100%;
    }
    &__content {
        width: 100%;
        height: 100vh;
        margin-top: auto;
    }
    &.fadeUppText{
        .section-4{
            &__wrapper{
                &-left{
                    &-container {
                        opacity: 1;
                        transform: translateY(0) !important ;
                    }
                }
            }
        }
    }
    &__wrapper{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;

        &-left{
            width: 36%;
            height: 100%;
            // padding: 20px;
            padding-right: 50px;
            padding-left: calc(((100% - 1500px) / 2));
            z-index: 10;
            background-color: #fff;
            

            &-container {
                position: relative;
                height: 100%;
                display: flex;
                justify-content: center;
                flex-direction: column;
                opacity: 0;
                transition: 0.7s ease-in-out;
                transform: translateY(50px);
            }
            .swiper-button-container{
                position: absolute;
                display: flex;
                column-gap: 20px;
                top: 74%;
            }
            .swiper-button-prev{
                width: 80px;
                height: 80px;
                border: none;
                border-radius: 50%;
                background-color: var(--c-secondary);
                z-index: 10;
                opacity: 0.75;
                font-size: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.3s ease;
                &::before{
                    content: "\f053";
                    font-family: "Font Awesome 5 Free";
                    font-weight: 900;
                    font-size: 18px;
                    color: #fff;
                }
            }
            .swiper-button-next{
                width: 80px;
                height: 80px;
                border: none;
                border-radius: 50%;
                background-color: var(--c-secondary);
                z-index: 10;
                opacity: 0.75;
                font-size: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.3s ease;

                &::before{
                    content: "\f054";
                    font-family: "Font Awesome 5 Free";
                    font-weight: 900;
                    font-size: 18px;
                    color: #fff;
                }
            }
            .swiper-button-next,
                .swiper-button-prev {
                    &:hover{
                        opacity: 1;
                        cursor: pointer;
                    }
                }
        }
        &-right{
            height: 100%;
            width: 64%;
            // background-image: url(./../../img/zabor-4.png);
            // background-repeat:no-repeat;
            // background-position: center;
            // background-size: cover;
           &-swiper{
                position: relative;
                width: 100%;
                height: 100%;
                z-index: 1;
                display: flex;
                box-sizing: content-box;
            }


            &-wrapper{
                display: flex;
                height: 100%;
            }
            &-slide{
                height: 100%;
                width: 100%;
            }
        }
    }
    &-title{
        color: #555555;
        margin-bottom: 50px;
    }
    &-descr{
        font-size:  var(--fz-m);
        color: #555555;
        margin-bottom: 120px;
    }
    

}
